import React from 'react';

const FontWeight = () => {
  const fontWeights = [100, 200, 300, 400, 500, 600, 700, 800, 900];
  return (
    <div className="font-weight">
      <div className="font-weight-container">
        {fontWeights.map(ele => (
          <p className="font-weight-normal" style={{ fontWeight: ele }}>{`${ele}`}</p>
        ))}
        <p></p>
      </div>
      <div className="font-weight-container" style={{ fontFamily: 'my' }}>
        {fontWeights.map(ele => (
          <p className="font-weight-defined" style={{ fontWeight: ele }}>{`${ele}`}</p>
        ))}
        <p>@font-facefont-weight</p>
      </div>
    </div>
  );
};

export default FontWeight;
